<template>
	<view class="detail-wrap">
		<detail-info :item="item"></detail-info>
		
		<view class="u-comment-title">最新评论 {{comment.count}}</view>
		<!-- 评论列表 子评论紧贴父评论后面 -->
		<view class="uni-comment u-comment">
			<block v-for="(item,index) in comment.list" :key="index">
				<comment-list :item="item" :index="index"></comment-list>
			</block>
		</view>
		
		<!-- 底部输入框 -->
		<user-input @submit="submit"></user-input>
		
		<!-- 分享 -->
		<share-comp :isShowModel="isShowModel" @close-model="closeModel"></share-comp>
	</view>
</template>

<script>
	import UserInput from '../../components/user-input/user-input.vue'
	import DetailInfo from '../../components/detail-info/detail-info.vue'
	import CommentList from '../../components/detail/comment-list.vue'
	import getTime from '../../common/time.js'
	import ShareComp from '../../components/share/share.vue'
	export default {
		components:{
			DetailInfo,
			CommentList,
			UserInput,
			ShareComp
		},
		data() {
			return {
				item: {},
				comment:{
					count:20,
					list:[]
				},
				isShowModel: false
			}
		},
		onLoad(res) {
			let item = JSON.parse(res.item);
			uni.setNavigationBarTitle({
				title: item.title || "详情"
			})
			
			uni.showLoading({
				title: "Loading...",
				mask: true
			})
			
			//请求数据
			this.getData(item)
			this.getcomment()
		},
		onNavigationBarButtonTap(e) {
			// console.log(e.index)
			if(e.index === 0){
				// uni.showToast({
				// 	title:"你想操作啥？"
				// })
				this.isShowModel = true;
			}else{
				uni.navigateBack({
					delta:1
				})
			}
		},
		methods: {
			getData(item) {
				this.item = { //图文
					id: item.id,
					userPic: item.userPic,
					userName: item.userName,
					isguanzhu: item.isguanzhu,
					title: item.title,
					type: item.type, // image 和 video  两种类型
					titlePic: item.titlePic, //封面图
					infoNum: {
						index: item.infoNum.index || 0, //0未顶踩  1顶   2踩
						dingNum: item.infoNum.dingNum || 0,
						caiNum: item.infoNum.caiNum || 0
					},
					// infoNum: item.infoNum,
					commentNum: item.commentNum,
					shareName: item.shareName,
					//下面获取 ------
					content: ''  ,
					picList: [],
					sex: 0,
					age: 0,
					create_time: 0
				};
				//获取文章其他内容
				this.getDetails()
			},
			async getDetails(){
				let [err, res] = await this.$http.get('/post/'+this.item.id);
				
				//验证错误
				if(this.$http.errorCheck(err, res)){
					uni.hideLoading();
					return
				}
				
				let data = res.data.data.detail;
				//内容 content
				this.item.content = data.content;
				//图片预览列表
				data.images.forEach(item => {
					this.item.picList.push(item.url)
				})
				//年龄性别
				this.item.sex = data.user.userinfo.sex;
				this.item.age = data.user.userinfo.age;
				//创建时间
				this.item.create_time = data.create_time;
				uni.hideLoading();
			},
			getcomment(){
				let arr=[
					// 一级评论
					{
						id:1,
						fid:0,//父级id
						userpic:"https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
						username:"小猫咪",
						time:"1555400035",
						data:"支持国产，支持DCloud!",
					},
					// 子级评论
					{
						id:2,
						fid:1,
						userpic:"https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
						username:"小猫咪",
						time:"1555400035",
						data:"支持国产，支持DCloud!",
					},
					{
						id:3,
						fid:1,
						userpic:"https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
						username:"小猫咪",
						time:"1555400035",
						data:"支持国产，支持DCloud!",
					},
					{
						id:4,
						fid:0,
						userpic:"https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
						username:"小猫咪",
						time:"1555400035",
						data:"支持国产，支持DCloud!",
					},
				];
				for (let i = 0; i < arr.length; i++) {
					arr[i].time=getTime.gettime.gettime(arr[i].time);
				}
				this.comment.list=arr;
			},
			submit(text){
				let obj = {
					
						id:1,
						fid:0,//父级id
						userpic:"https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
						username:"小猫咪",
						time:getTime.gettime.gettime(new Date().getTime()),
						data: text,
					
				};
				this.comment.list.push(obj)
				console.log(text)
			},
			closeModel(){
				this.isShowModel = false;
			}
		}
	}
</script>

<style scoped lang="scss">
/* 评论 */	
.u-comment{
	padding: 0 20upx;
}
.u-comment-title{
	padding: 20upx;
	font-size: 30upx;
	font-weight: bold;
}
.detail-wrap {
	padding-bottom: 100upx;
	
	
}
</style>
